<template>
  <NavbarMainVue @searchValue="searchValue" />
  <div
    class="home w-100"
    :class="{ modeDark: mode }"
    style="margin-top: 75px; transition: 0.5s; min-height: 100vh"
  >
    <div
      class="progress position-fixed start-0 w-100 z-3"
      :class="{ progressDark: mode }"
      role="progressbar"
      aria-label="Example with label"
      aria-valuenow="25"
      aria-valuemin="0"
      aria-valuemax="100"
      style="border-radius: 0%"
    >
      <div
        class="progress-bar"
        :class="{ progressBarDark: mode }"
        :style="`width: ${progress}%`"
      >
        {{ progress }}%
      </div>
    </div>
    <div
      style="min-height: 100vh"
      class="d-flex pt-3 flex-wrap justify-content-center align-items-center w-100"
    >
      <div
        class="cardsNews w-100 d-flex flex-wrap justify-content-center align-items-center"
        v-if="listNews"
        style="min-height: 100vh"
      >
        <CardSingleNews
          v-for="(newsObject, index) in listNews"
          :key="index"
          :newsObject="newsObject"
          :usersList="usersList"
        />
      </div>
      <div v-if="!top" class="d-flex justify-content-center pb-3">
        <div
          class="spinner-border p-3 fs-3"
          style="width: 5rem; height: 5rem; color: #c35555"
          role="status"
        >
          <span class="visually-hidden fs-1">Loading...</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api/api";
import NavbarMainVue from "@/components/navbarMain/NavbarMain.vue";
import CardSingleNews from "@/components/CardSingleNews.vue";
import LodingSpinnerVue from "@/components/LodingSpinner.vue";
export default {
  name: "pormoView",
  data() {
    return {
      trend: "News",
      page: 1,
      listNews: [],
      top: false,
      usersList: [],
      progress: 20,
      // message: "",
    };
  },
  // views downloads likes comments
  components: {
    NavbarMainVue,
    CardSingleNews,
    LodingSpinnerVue,
  },
  methods: {
    searchValue(trend) {
      this.trend = trend;
      this.listNews = [];
      this.page = 1;
      this.progress = 20;
      this.top = false;
      this.scrollUpdate();
      this.listdataNews({ trend: this.trend, page: this.page });
    },
    async listdataNews({ trend, page }) {
      const list = await api.getListImages({
        trend: trend,
        page: page,
      });
      this.listNews = this.listNews.concat(list.hits);
      this.usersList = this.listNews.map(({ user, userImageURL }) => ({
        userImageURL,
        userName: user,
      }));
    },
    scrollUpdate() {
      scroll({
        top: 0,
      });
    },
  },
  computed: {
    mode() {
      return this.$store.state.modeSite == "Dark mode";
    },
  },
  watch: {
    trend: {
      immediate: true,
      handler() {
        this.listdataNews({ trend: this.trend, page: this.page++ });
      },
    },
  },
  mounted() {
    this.scrollUpdate();
    addEventListener("scroll", () => {
      if (
        window.innerHeight + window.scrollY >= document.body.offsetHeight &&
        !this.top
      ) {
        if (this.page < 5) {
          this.listdataNews({
            trend: this.trend,
            page: this.page++,
          });
          this.progress += 20;
        } else {
          this.listdataNews({
            trend: this.trend,
            page: this.page,
          });
          this.top = true;
          this.progress = 100;
        }
      }
    });
  },
};
</script>

<style scoped>
.modeDark {
  background: rgba(0, 0, 0, 0.829);
  color: white;
}
.btn {
  border: none;
}
.icon {
  font-size: 22px;
}
.btn:active span {
  color: blue;
}
::-webkit-scrollbar {
  width: 3px;
}
.progressDark {
  background: rgb(80, 80, 80);
}
.progressBarDark {
  background: red;
}
</style>
