<template>
  <div :class="{ darkMode: mode }" class="comments d-flex align-items-center">
    <div v-if="commentUser.userImageURL">
      <img
        style="width: 20px; height: 20px; border-radius: 50%"
        :src="commentUser.userImageURL"
      />
      <span class="mx-1" style="font-size: 10px">{{
        commentUser.userName
      }}</span>
      <div
        v-if="commentUser.comment"
        class="rounded p-1 ms-3"
        style="background: rgba(226, 226, 226, 0.768); font-size: 13px"
      >
        {{ commentUser.comment }}
      </div>
      <div
        v-else
        class="rounded p-1 ms-3"
        style="background: rgba(226, 226, 226, 0.768); font-size: 13px"
      >
        Beautiful<i class="fa-regular fa-face-smile-beam ms-2"></i>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "cardSingleNews",
  props: {
    commentUser: {
      userName: String,
      userImageURL: String,
    },
    comment: String,
  },
  computed: {
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
  },
};
</script>
