<template>
  <div
    v-if="message"
    aria-label="Close"
    id="Close"
    class="alert w-100 alert-danger position-fixed start-0 z-3"
    style="top: 76px"
    role="alert"
  >
    <button type="button" class="btn-close me-3" @click="message = ''"></button>
    <span>{{ message }}<i class="fa-regular fa-face-frown ms-2"></i></span>
  </div>
  <div
    :class="{ modeDarkCard: mode }"
    class="card m-2 d-flex pb-3"
    style="width: 280px; min-height: 400px"
  >
    <img
      :src="newsObject.webformatURL"
      class="img-fluid rounded-start"
      v-if="newsObject.webformatURL"
      style="width: 100%; height: 150px; object-fit: cover"
    />
    <span class="w-100 text-center pt-2" style="font-size: 80px" v-else
      ><i class="fa-solid fa-circle-exclamation" style="color: #c35555"></i>
      <p class="fs-1">Not</p></span
    >
    <div class="card-body">
      <h5 class="card-title">{{ newsObject.title }}</h5>
      <div class="card-text">
        <p class="p-0 m-0">
          <span class="fs-5 m-0 p-0" style="color: rgb(191, 27, 27)"
            >Event documenter:</span
          >
          {{ newsObject.user }}
        </p>
        <p class="p-0 m-0">
          <span class="fs-5 m-0 p-0" style="color: rgb(191, 27, 27)"
            >snippet :
          </span>
          {{ newsObject.tags }}
        </p>
        <p class="p-0 m-0">
          <span class="fs-5 m-0 p-0" style="color: rgb(191, 27, 27)"
            >source :
          </span>
        </p>
      </div>
      <div
        class="w-100 position-absolute d-flex justify-content-between align-items-center p-2"
        style="bottom: 0px; left: 0px"
      >
        <a class="link-opacity-100" :href="newsObject.imageURL">Link detials</a>
        <div
          class="interaction d-flex justify-content-between align-items-center"
        >
          <button
            @click="likeProcessing($refs.likes)"
            ref="likes"
            class="btn me-1 p-0"
            :class="{ interactionDark: mode }"
          >
            <span class="icon"
              ><i class="fa-regular fa-thumbs-up mx-1"></i></span
            >{{ newsObject.likes }}
          </button>
          <button
            class="btn comments"
            type="button"
            data-bs-toggle="collapse"
            :data-bs-target="`#comment${newsObject.id}`"
            aria-expanded="false"
            aria-controls="collapseExample"
            :class="{ interactionDark: mode }"
          >
            <span class="icon"
              ><i class="fa-regular fa-comments mx-1"></i
            ></span>
            {{ newsObject.comments }}
          </button>
        </div>
      </div>
      <div class="collapse pb-3 w-100 m-0" :id="`comment${newsObject.id}`">
        <div
          class="card card-body overflow-y-auto position-relative w-100"
          style="height: 150px"
          :class="{ modeDarkCardcomments: mode }"
        >
          <!-- v-for="(use, index) in users"
          :key="index" -->
          <CommentUser
            v-for="(use, index) in usersNews"
            :key="index"
            :commentUser="use"
            :comment="comment"
          />
          <div
            class="d-flex align-items-center p-1 px-0 pb-0 mt-3 w-100"
            style="bottom: 40px"
          >
            <!--  -->
            <img
              class="mx-1"
              v-if="detailsUser.imgUser"
              :src="detailsUser.imgUser"
              style="
                width: 30px;
                height: 30px;
                object-fit: cover;
                border-radius: 50%;
              "
            />
            <span v-else class="fs-3 mx-1"
              ><i class="fa-regular fa-face-frown"></i
            ></span>
            <input
              type="text"
              ref="comment"
              class="form-control p-1"
              id="validationCustom01"
              placeholder="comment"
              v-model="comment"
              @keyup.enter="addComment"
              :class="{ inputDark: mode }"
            />
            <button
              class="btn p-0 ms-1"
              style="border: none; border-radius: 50%"
              @click="showEmoji"
            >
              <span> &#128516;</span>
            </button>
            <div
              class="emoji position-absolute d-flex align-items-center overflow-hidden"
              :style="`width:${widthDivEmoji}%; transition:0.5s`"
            >
              <span
                @click="addEmojiInComment('&#128512;')"
                class="ps-1"
                style="border-radius: 50%; cursor: pointer"
              >
                &#128512;</span
              >
              <span
                @click="addEmojiInComment('&#128513;')"
                class="ps-1"
                style="border-radius: 50%; cursor: pointer"
              >
                &#128513;</span
              >
              <span
                @click="addEmojiInComment('&#128514;')"
                class="ps-1"
                style="border-radius: 50%; cursor: pointer"
              >
                &#128514;</span
              >
              <span
                @click="addEmojiInComment('&#128517;')"
                class="ps-1"
                style="border-radius: 50%; cursor: pointer"
              >
                &#128517;</span
              >
              <span
                @click="addEmojiInComment('&#128151;')"
                class="ps-1"
                style="border-radius: 50%; cursor: pointer"
              >
                &#128151;</span
              >
              <span
                @click="addEmojiInComment('&#128525;')"
                class="ps-1"
                style="border-radius: 50%; cursor: pointer"
              >
                &#128525;</span
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CommentUser from "./CommentUser.vue";
export default {
  name: "cardCommentsUsers",
  props: {
    newsObject: {
      id: Number,
      tags: String,
      webformatURL: String,
      views: Number,
      downloads: Number,
      likes: Number,
      comments: Number,
      user: String,
      userImageURL: String,
    },
    usersList: Array, //usersList => user: {userName , userImageURL}
  },
  data() {
    return {
      message: "",
      comment: "",
      usersNews: [...this.usersList],
      widthDivEmoji: 0,
    };
  },
  methods: {
    addComment() {
      if (this.detailsUser.email != "" && this.comment != "") {
        this.usersNews.push({
          userName: this.detailsUser.name,
          userImageURL: this.detailsUser.imgUser,
          comment: this.comment,
        });
        this.detailsUser.interaction.push({
          idInteraction: this.newsObject.id,
          type: "comment",
          comment: this.comment,
        });
        this.comment = "";
        this.newsObject.comments++;
      } else if (this.detailsUser.email == "")
        this.message = "You cannot comment if you are not logged in";
    },
    likeProcessing(eleLike) {
      eleLike.classList.toggle("onLike");
      if (eleLike.classList.toString().includes("onLike")) {
        this.newsObject.likes += 1;
      } else this.newsObject.likes -= 1;
    },
    showEmoji() {
      if (this.widthDivEmoji == 0) {
        this.widthDivEmoji = 70;
      } else {
        this.widthDivEmoji = 0;
      }
    },
    addEmojiInComment(emoji) {
      this.comment = `${this.comment}${emoji}`;
      this.widthDivEmoji = 0;
      this.$refs.comment.focus();
    },
  },
  computed: {
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
    detailsUser() {
      if (this.$store.state.indexLoginUser != null) {
        return this.$store.state.users[this.$store.state.indexLoginUser];
      } else return this.$store.state.user;
    },
  },
  components: {
    CommentUser,
  },
  mounted() {
    this.detailsUser.interaction.forEach((element) => {
      if (
        element.type == "comment" &&
        element.idInteraction == this.newsObject.id
      ) {
        this.usersNews.push({
          userName: this.detailsUser.name,
          userImageURL: this.detailsUser.imgUser,
          comment: element.comment,
        });
      }
    });
    //
  },
};
</script>
<style scoped>
.modeDarkCard {
  background: rgb(34, 34, 34);
  color: rgb(197, 197, 197);
}
.interactionDark {
  color: white;
}
.form-control:focus {
  box-shadow: 0 0 0 0.1rem rgba(82, 149, 248, 0.25);
}
.onLike {
  color: blue;
}
.comments:focus {
  color: blue;
}
.emoji {
  height: 35px;
  margin: auto;
  background-color: #bad0b7;
}
.modeDarkCardcomments {
  background: #3d3d3d;
  color: white;
}
.inputDark {
  background: #b9b9b9;
  border: none;
  color: white;
}
</style>
